.Categories {
  min-height: calc(100vh - 100px);
  background-color: #aaaaaa;
  min-width: 320px;

  .title {
    padding-top: 45px;
    min-width: 190px;
    font-family: Circe, Arial, sans-serif;
    font-weight: 700;
    font-size: 2em;
    text-align: center;
    line-height: 1.22222em;
  }
  .body {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    margin: 0 20px;
  }
  .arr {
    max-width: 100%;
    margin-bottom: 20px;
    &_title {
      font-family: Circe, Arial, sans-serif;
      font-weight: 600;
      margin-bottom: 10px;
      font-size: 1.5em;
    }
    &_body {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
    }
    &_link {
      text-decoration: none;
      color: unset;
    }
    &_block {
      margin-right: 10px;
      margin-bottom: 10px;
      width: 200px;
      height: 340px;
      box-shadow:
               inset 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
               inset -8px -8px 12px 0 rgba(255, 255, 255, 0.3);

      overflow: hidden;
      &_image {
        width: 100%;
        height: 200px;
        background-repeat: no-repeat;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      img {
        //width: auto;
        max-height: 90%;
        //background-color: #aaaaaa;
      }
      &_legend {
        padding: 10px;

        &_articular {
          -webkit-user-select: contain;
          -ms-user-select:contain;
          user-select: contain;
          color: #292929;
          font-weight: 400;
          font-size: 0.8em;
        }
        &_price {
          padding-top: 10px;

          span {
            font-family: Circe, Arial, sans-serif;
            font-weight: 400;
            font-size: 1.2em;
          }
        }

        &_description {
          padding-top: 10px;

          font-size: 13px;
          line-height: 19px;
          overflow: hidden;
          height: 60px;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .Categories {
    min-height: 100vh;
  }
}
@media (max-width: 477px) {
  .arr_body {

    display: grid !important;
    grid-template-columns: 1fr 1fr;


  }
  .body {
    margin:  0 10px !important;

  }
  .arr_block {
    width: 45vw !important;
    min-width: 140px;
  }
}
